// --- Base Includes ---
export const INCLUDE_PERSON = { person: true };
export const INCLUDE_DEPARTMENT = { department: true };
export const INCLUDE_ROLE = { role: true };
export const INCLUDE_PRODUCT_AND_SERVICE = { product: true, service: true };

// --- Employee Includes ---
export const INCLUDE_EMPLOYEE_BASE = {
  ...INCLUDE_DEPARTMENT,
  ...INCLUDE_ROLE,
};

// --- Representative / Person Includes ---
export const INCLUDE_REPRESENTATIVE = { include: INCLUDE_PERSON };

// --- Company / Client Includes ---
export const INCLUDE_COMPANY_REPRESENTATIVES = {
  representatives: INCLUDE_REPRESENTATIVE,
};
export const INCLUDE_CLIENT_DETAILS = {
  ...INCLUDE_PERSON,
  company: { include: INCLUDE_COMPANY_REPRESENTATIVES },
};

// --- Department Includes ---
export const INCLUDE_DEPARTMENT_DETAILS = {
  employees: { include: INCLUDE_EMPLOYEE_BASE },
};

// --- Ticket Includes ---
export const INCLUDE_TICKET_BASE = INCLUDE_PRODUCT_AND_SERVICE;

export const INCLUDE_TICKET_DETAILS = {
  employees: { include: INCLUDE_EMPLOYEE_BASE },
  client: { include: INCLUDE_CLIENT_DETAILS },
  representative: { include: INCLUDE_PERSON },
};

// --- Employee with Tickets ---
export const INCLUDE_EMPLOYEE_WITH_TICKETS = {
  ...INCLUDE_EMPLOYEE_BASE,
  tickets: { include: INCLUDE_TICKET_DETAILS },
};

// --- Activity Log Includes ---
export const INCLUDE_ACTIVITY_LOG = {
  author: { include: INCLUDE_EMPLOYEE_BASE },
};

// --- Task Includes ---
export const INCLUDE_TASK_DETAILS = {
  assignee: { include: INCLUDE_EMPLOYEE_BASE },
  taskComments: true,
};

// --- Role Includes ---
export const INCLUDE_ROLE_WITH_EMPLOYEES = {
  employees: { include: INCLUDE_EMPLOYEE_BASE },
};
